<template>
  <v-container id="dashboard-view" fluid tag="section">
    <v-row justify="center">
      <v-col cols="12">
        <material-card color="primary" icon="mdi-wallet">
          <template #title>
            <h2>ALL TRANSACTIONS</h2>
          </template>
          <v-container class="py-0">
            <v-data-table
              :headers="headers"
              :items="transactions"
              class="elevation-1"
              loading-text="Loading... Please wait"
              v-if="search == ''"
            >
              <!--verified or not-->
              <template v-slot:item.status="{ item }">
                <v-chip v-if="item.status == 1" color="green" label>
                  Verified
                </v-chip>
                <v-chip v-else color="warning" label>
                  Not verified
                </v-chip>
              </template>
              <template v-slot:item.verify="{ item }">
                <!-- {{item.createdAt}} -->
                <v-btn
                  small
                  v-if="item.status == 1"
                  color="success"
                  class="my-1 mx-2"
                >
                  Transaction Verified
                </v-btn>
                <v-btn
                  v-else
                  small
                  color="success"
                  class="my-1 mx-2"
                  @click="verifyPayment(item.reference)"
                >
                 Verify Transaction
                </v-btn>
              </template>
            </v-data-table>
            <!--------------search campaigns---------------------->
          </v-container>
        </material-card>
      </v-col>
    </v-row>

    <!-- Modals -->
    <!-- Approve Campaign dialogue-->
    <v-dialog
      v-model="verifyModal"
      transition="dialog-top-transition"
      max-width="400"
    >
      <v-card max-width="400">
        <v-toolbar color="success" dark>
          <v-icon>mdi mdi-warning </v-icon> {{ modalText.headerText }}
          <v-btn
            icon
            absolute
            right
            outlined
            class="float-right"
            color="white"
            @click="verifyModal = false"
          >
            <v-icon>mdi mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <p class="pt-4 text-center text-h3">
            {{ modalText.questionText }} <br />
          </p>
        </v-card-text>
      </v-card>
    </v-dialog>
    <!--Delete Modal-->
  </v-container>
</template>

<script>
let camApp={
  username:"7ZfFZ4VV-ybWZSWJL72FRlTA6agHj81vVrmNgeJ33e55j9dqdg43kQs7k6zZ4_THXljuPUMqqddNe4q-_HcB0w",
  passsword:"4xL1Rq_5Vj6oEIfBtWGFn6BJyVDzUU_SaZR1kcrByU0XyXvVGmsoXzuE4683dDpVreBr-xq7ZgOiTJbv8LXXkw",
}
import { mapGetters } from "vuex";
import axios from "axios";
let digitadApps = {
  admin: "https://portal.digitadooh.com", //https://digitad.us-east-2.elasticbeanstalk.com
  users: "https://app.digitadooh.com",
  piApp: "https://ourcore.digitadooh.com",
  storage: "https://storage.digitadooh.com",
};
export default {
  name: "DashboardView",

  data: () => ({
    id: "",
    search: "",
    approveModal: false,
    verifyModal: false,
    headers: [
      { text: "ID", value: "id" },

      {
        text: "Reference",
        align: "start",
        value: "reference",
      },
      { text: "Date", value: "date" },
      { text: "Operator", value: "operator" },
      { text: "Amount", value: "amount" },
      { text: "Status", value: "status" },
       { text: "Verification", value: "verify" },
    ],
    currentCampaign: {},
    modalText: {
      headerText: "",
      actionText: "",
      questionText: "",
    },
  }),
  methods: {
    verifyPayment(ref){
      let verifyModal=this.verifyModal;
      let modalText=this.modalText;
      let wallet=this.wallet[0];
      let walletBalance=wallet.balance;
      let walletId=wallet.id;
      let user_id = this.userDetails.user_id;
      let email=this.userDetails.email;
      //verify transaction
      ///
              var settings = {
              "url": "https://www.campay.net/api/token/",
              "method": "POST",
              "timeout": 0,
              "headers": {
                "Content-Type": "application/json"
              },
              "data": JSON.stringify({
                "username":camApp.username,
                "password":camApp.passsword
              }),
            };

        $.ajax(settings).done(function (response) {
          let token=response.token;
          if(token!==""){
              let authString=`Token ${token}`;
              //verify transaction
              var config = {
                method: 'get',
                url: 'https://www.campay.net/api/transaction/'+ref,
                headers: { 
                  'Authorization':authString, 
                  'Content-Type': 'application/json'
                }
              };

              axios(config)
              .then(function (response) {
                let feedBack=response.data;
                console.log(feedBack.status)
                if(feedBack.status=='SUCCESSFUL'){
                  //toggle buttons
                  let reference=feedBack.reference;
                  let status=feedBack.status;
                  let amount=parseInt(feedBack.amount);
                  let operator=feedBack.operator;
                  let newBalance=parseFloat(walletBalance)+parseInt(amount);
                  //
                  let formData= new FormData();
                    formData.append("reference",reference);
                    formData.append("amount",amount);
                    formData.append("user_id",user_id);
                    formData.append("email",email);
                    formData.append("walletBalance",newBalance);
                    formData.append("walletId",walletId);
                    formData.append("method",operator);
                    //store transaction details and fund wallet
                    const params = { crossDomain: true, withCredentials: true };
                    const api = axios.create({ baseURL: digitadApps.users });
                    api
                    .post(digitadApps.users + "/server/fundWallet.php",formData)
                    .then((result)=>{
                      //console.log(result.data)
                        if(result.data==1){
                            verifyModal=true;
                            modalText.headerText="Verifying transaction";
                            modalText.questionText="Verification successful";
                            location.reload();
                        }else{
                           verifyModal=true;
                            modalText.headerText="Verifying transaction";
                            modalText.questionText="Failed to fund wallet, contact support immediately!";
                        }
                    })
                    .catch((error)=>{
                       verifyModal=true;
                        modalText.headerText="Verifying transaction";
                        modalText.questionText=error.message;
                    })
                  //
                }else{
                    verifyModal=true;
                    modalText.headerText="Verifying transaction";
                    modalText.questionText="Transaction pending...";
                }
                
              })
              .catch(function (error) {
                console.log(error);
              });
          }
        });
    },
    campaignDetails(item) {
      let id = item.id;
      this.viewedCampaign.push(item);
      this.$router.push(`/campaign/${id}`);
    },
    setCurrentCampaign(campaign) {
      this.currentCampaign = campaign;
    },
  },
  computed: mapGetters([
    "transactions",
    "userDetails",
    "screensInfo",
    "wallet"
  ]),
};
</script>
<style>
.card-elm:hover {
  border: 2px solid orange;
}
.map {
  background-image: url("https://www.nationsonline.org/maps/germany-administrative-map.jpg");
  background-color: #cccccc;
  background-size: 100%;
  width: 100%;
  min-height: 700px;
}
.v-dialog--fullscreen {
  margin-top: 3% !important;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;

  /* margin: 5.2%; */
}
.cursor {
  cursor: pointer;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.task-btn {
  margin: 10px;
}
</style>
